*{
  margin: 0;
padding: 0;
box-sizing: border-box;

}

.content{
  height: 80vh;
}

.navbar{
  background-color: blue;
}
.Register-Page{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80vh;
}

.filters{
  display: flex ;
  align-items: center ;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px;
}

.anticon svg{
  font-size: 20px;
  cursor: pointer;
}

.active-icon{
color: black;
}

.inactive-icon {
color: gray;
}
